// Fonts
@font-face {
    font-family: 'Telefonica';
    font-style: normal;
    font-weight: 200;
    src: local('Telefonica Extra Light'),
         url('../fonts/Telefonica/Telefonica-ExtraLight.woff2') format('woff2'), 
         url('../fonts/Telefonica/Telefonica-ExtraLight.woff') format('woff'),
         url('../fonts/Telefonica/Telefonica-ExtraLight.ttf') format('truetype');
}

