.nat-bannerEvo{
    position: relative;    
    .slider{
        margin: 0;
        height: 400px;
    }
    .slider__item{
        height: 400px;
        position: relative;
        text-align: left;
        .item__sticker{
            position: absolute;
            right: 0;
            z-index: 0;
            bottom: 0;
            .sticker{
                display: block;
            }
            .sticker__image{
                display: block;
            }
        }
        .item__picture{
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            z-index: 0;
            width: 100%;
            height: 100%;
        }
        .item__container{
            height: 100%;
            display: flex;
            flex-wrap: wrap;
            position: relative;
            z-index: 1;
            .container{
                width: 100%;
                padding: 46px 20px;
                @media only screen and (min-width: 992px){
                    padding: 60px 40px;
                }
            }
            .container__free-html{
                width: 100%;            
                max-width: 626px;
            }
        }
        .container__header{
            width: 100%;            
            max-width: 626px;
        }
        .container__body{
            max-width: 295px;
            position: relative;
            @media only screen and (min-width: 992px){
                max-width: 626px;
            }  
        }
        .container__body.container__body--medium{
            @media only screen and (min-width: 992px){
                max-width: 313px;
            }  
        }
        .container__header{
            margin: 0 0 18px;
            display: inline-block;
            .badge{
                @include root-tipography;
                font-size: 16px;
                line-height: 24px;
                color: #ffffff;
                height: 24px;
                text-transform: uppercase;
                float: none;
                display: inline-block;
                margin: 0;
                width: auto;
                min-width: 120px;
                padding: 0 11px;
                @media only screen and (min-width: 992px){
                    font-size: 18px;
                    line-height: 24px;
                }
                i{
                    margin-right: 5px;
                    font-size: 12px;
                    display: inline-block;
                    vertical-align: baseline;
                    @media only screen and (min-width: 992px){
                        font-size: 15px;
                    }
                    &::before{
                        color: #ffffff;
                    }

                }
            }
        }
        .container__body{
            .title{
                @include root-tipography-regular;
                font-size: 32px;
                line-height: 40px;
                margin: 0;
                @media only screen and (min-width: 992px){
                    font-size: 40px;
                    line-height: 50px;
                }
            }
            .nat__title--summerloving,
            .nat__title--kokomo{
                font-size: 100px;
                line-height: 40px;
                text-align: left;
                margin: 20px 0 35px;
                @media only screen and (min-width: 992px){
                    font-size: 150px;
                    line-height: 61px;
                    margin: 23px 0 50px;
                }
            }
            .nat__title--guthen{
                font-size: 70px;
                line-height: 0;
                text-align: left;
                margin: 30px 0 55px;
                @media only screen and (min-width: 992px){
                    font-size: 100px;
                    line-height: 61px;
                    margin: 23px 0 40px;
                }
            }
            .title--large{
                font-size: 32px;
                line-height: 40px;
                margin-bottom: 30px;
                @media only screen and (min-width: 992px){
                    font-size: 64px;
                    line-height: 48px;
                    margin-bottom: 36px;
                }
            }
            .text{
                @include root-tipography-regular;
                font-size: 16px;
                line-height: 20px;
                margin: 15px 0 0;
                width: 180px;
                display: block;
                @media only screen and (min-width: 992px){
                    font-size: 24px;
                    line-height: 28px;
                    width: auto;
                }
            }
            .btn{
                @include root-tipography;
                font-size: 16px;
                line-height: 40px;
                text-transform: uppercase;
                width: auto;
                height: 40px;
                padding: 0 24px;
                min-width: 120px;
                margin-top: 29px;
                box-shadow: inherit;
                border-radius: 200px;
                letter-spacing: 0.0125em;
                @media only screen and (min-width: 992px){
                    margin-top: 43px;
                }
                .btn__icon{
                    position: relative;
                    margin-left: -14px;
                    margin-right: 20px;
                    display: inline-block;
                    vertical-align: text-top;
                }
                .btn__txt{
                    float: right;
                }
            }
        }
    }
    .slider__item.justify-items--center{
        .picture .picture__image{
            object-position: center;
        }
        .container{
            width: 100%;
            max-width: 626px;
            margin: 0 auto;
            padding: 44px 42px;
            @media only screen and (min-width: 992px){
                padding: 67px 42px;
            }
        }
        .container__header,
        .container__body{
            max-width: inherit;
            width: 100%;
            text-align: center;
        }
        .container__body{
            .text{
                width: 100%;
            }
        }
    }
    .slider__item.justify-items--end{
        .picture .picture__image{
            object-position: left;
        }
        .container{
            text-align: right;
        }
        .container__body{
            margin-left: auto;
            .text{
                margin-left: auto;
            }
        }
    }
    .slider__item.custom-object-fit{
        height: 100vh;
        background-size: cover;
        max-height: 400px;
        position: relative;
        background-position: right;              
        .picture__image{
            opacity: 0;
        }
    }
    .picture{
        .picture__image{
            object-fit: cover;
            object-position: right top;
            height: 100%;
            max-width: 100%;
            width: 100%;
        }
    }
    .slider__anchor{
        position: absolute;
        width: auto;
        bottom: 42px;
        z-index: 1;
        text-align: center;
        margin: 0 auto;
        left: 50%;
        transform: translate(-50%, 0);
        .anchor{
            display: block;
        }
        .icon{
            font-size: 16px;
            color: #d3d4d3;
            display: block;
            svg{
                display: inline-block;
                font-size: inherit;
                width: 1em;
                height: 1em;
                overflow: visible;
                stroke-width: 0;
                stroke: currentColor;
                fill: currentColor;
            }
        }
    }
    .anchor__right{
        @media (max-width: 600px) {
            left: inherit;
            transform: inherit;
            right: 14px;
            bottom: 21px;
        }
    }
    .swiper-pagination{
        bottom: 24px;
    }
    .swiper-pagination-bullet{
        width: 10px;
        height: 10px;
        background: transparent;
        border: 1px solid #D3D4D3;
        opacity: 1;
    }
    .swiper-pagination-bullet-active{
        background: #5CB615;
    }
}