// Fonts
@font-face {
    font-family: 'Guthen-bloots-basic';
    font-style: normal;
    font-weight: normal;
    src: local('Guthen-Bloots-Basic'),
         url('../fonts/guthenbloots/Guthen-Bloots-Basic.woff2') format('woff2'), 
         url('../fonts/guthenbloots/Guthen-Bloots-Basic.woff') format('woff'),
         url('../fonts/guthenbloots/Guthen-Bloots-Basic.ttf') format('truetype');
}

@font-face {
    font-family: 'kokomo_breezeregular';
    font-weight: normal;
    font-style: normal;
    src: url('../fonts/kokomo/kokomo_breeze_regular-webfont.woff2') format('woff2'),
         url('../fonts/kokomo/kokomo_breeze_regular-webfont.woff') format('woff');
}

@font-face {
    font-family: 'summer_lovingregular';
    font-weight: normal;
    font-style: normal;
    src: url('../fonts/summerlove/summer_loving_regular.woff2') format('woff2'),
         url('../fonts/summerlove/summer_loving_regular.woff') format('woff');
}