/* General styles for all types of buttons */
.progress-button {
	position: relative;
	display: inline-block;
	padding: 0 60px;
	outline: none;
	border: none;
	background: $color_cian;
	color: #fff;
	text-transform: uppercase;
	letter-spacing: 1px;
	font-size: 1em;
	line-height: 4;
}

.progress-button[disabled],
.progress-button[disabled].state-loading {
	cursor: default;
}

.progress-button .content {
	position: relative;
	display: block;
}

.progress-button .content::before,
.progress-button .content::after  {
	position: absolute;
	right: 20px;
	color: #ffffff;
	font-family: "icomoon";
	opacity: 0;
    transition: opacity 0.3s 0.3s;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.progress-button .content::before {
	content: "\e5ca"; /* Checkmark for success */
}

.progress-button .content::after {
	content: "\e5cd"; /* Cross for error */
}

.progress-button.state-success .content::before,
.progress-button.state-error .content::after {
	opacity: 1;
}

.notransition {
	transition: none !important;
}

.progress-button .progress {

    height: auto;
    position: initial;
    margin: 0;
    display: initial;
    width: auto;
    border-radius: inherit;
}

.progress-button .progress-inner {
	position: absolute;
	left: 0;
	background: $color_cian;
}

.progress-button[data-horizontal] .progress-inner {
	top: 0;
	width: 0;
	height: 100%;
	transition: width 0.3s, opacity 0.3s;
}

.progress-button[data-vertical] .progress-inner {
	bottom: 0;
	width: 100%;
	height: 0;
	transition: height 0.3s, opacity 0.3s;
}

.progress-button[data-perspective] {
	position: relative;
	display: inline-block;
	padding: 0;
	background: transparent;
	perspective: 900px;
}

.progress-button[data-perspective] .content {
	padding: 0 60px;
	background: $color_cian;
}

.progress-button[data-perspective] .progress-wrap {
	display: block;
	transition: transform 0.2s;
	transform-style: preserve-3d;
}

.progress-button[data-perspective] .content,
.progress-button[data-perspective] .progress {
	outline: 1px solid rgba(0,0,0,0); /* Smoothen jagged edges in FF */
}

/* Shrink horizontal */
/* ====================== */

.progress-button[data-style="shrink"] { /* common for horizontal and vertical */
	overflow: hidden;
	transition: all 0.2s;
}

.progress-button[data-style="shrink"][data-horizontal] .content {
	transition: opacity 0.3s, transform 0.3s;
}

.progress-button[data-style="shrink"][data-horizontal] .content::before,
.progress-button[data-style="shrink"][data-horizontal] .content::after {
	top: 100%;
	right: auto;
	left: 50%;
	transition: opacity 0.3s;
	transform: translateX(-50%);
}

.progress-button[data-style="shrink"][data-horizontal].state-loading {
	transform: scaleY(0.3);
}

.progress-button[data-style="shrink"][data-horizontal].state-loading .content {
	opacity: 0;
}

.progress-button[data-style="shrink"][data-horizontal].state-success .content,
.progress-button[data-style="shrink"][data-horizontal].state-error .content {
	transform: translateY(-100%);
}

/* ====================== */

@mixin btn-tipography {
    font-family: Telefonica;
    font-style: normal;
    font-weight: normal;   
}

@mixin root-btn{
    max-width: 176px;
    width: 100%;
    height: 48px;
    margin: auto;
    border-radius: 70px;
    display: block;
    @include root-tipography;
    font-size: 18px;
    line-height: 42px;
    text-align: center;
    box-shadow: inherit;
    padding: 0;
    background: $color_verde;
    border: 2px solid $color_verde;
    color: #ffffff;
}

.movistar--green{
    .btn.btn--primary{
        background-color: $color_verde;
    }
    .btn.state-loading{
        background-color: #9cef55;
        .progress-inner{
            background-color: $color_verde;
        }
    }
    
}
.movistar--blue{
    .btn.btn--primary{
        background-color: $color_cian;
    }
    .btn.state-loading{
        background-color: #72BFEB;
    }    
}
.btn.btn--primary{
    @include btn-tipography;
    font-size: 16px;
    line-height: 42px;
    height: 42px;
    padding-top: 0;
    width: auto;
    display: inline-block;
    box-shadow: inherit;
    border-radius: 4px;
    text-transform: initial;
    @media only screen and (min-width: 600px){
        font-size: 18px;
        padding: 0 35px;
        line-height: 48px;
        height: 48px;
    }
}


//Botones nueva linea Grafica
.btn__root{
    @include root-btn;
}
.btn__cian{    
    background: $color_cian;
    color: #ffffff;
    border: 2px solid $color_cian;
}
.btn__azul{    
    background: $color_azul;
    color: #ffffff;
    border: 2px solid $color_azul;
}
.btn__blanco{    
    background: #ffffff;
    color: $color_cian;
    border: 2px solid #ffffff;
}
.btn__flat{    
    background: transparent;
    color: #ffffff;
    border: 2px solid #ffffff;
}
.btn__flat--gris{    
    background: transparent;
    color: $color_texto;
    border: 2px solid $color_texto;
}
.btn__flat--cian{    
    background: transparent;
    color: $color_cian !important;
    border: 2px solid $color_cian;
}
.btn__flat--azul{    
    background: transparent;
    color: $color_azul;
    border: 2px solid $color_azul;
}

@mixin btn__borderless-color($color){
    color: $color;
    .seudo-text,.icon{
        color: $color;
    }
}
@mixin btn__borderless {
    @include root-tipography;
    font-size: 16px;
    line-height: 20px;
    text-align: center;    
    background: transparent;
    border: none;
    display: inline-block;
    margin: auto;
    width: 100%;
    max-width: 176px;
    height: auto;
    text-decoration: none;
    @media only screen and (min-width:992px){
        height: 50px;
        line-height: 50px;
    }
    .seudo-text:before{
        display: inline-block;
        content: 'más';
    }
    .icon{
        width: 18px;
        font-size: 18px;
        display: inline-block;
        vertical-align: text-bottom;
    }
    &:focus{
        outline: none;
        background: transparent;
    }
}

.btn__borderless{
    @include btn__borderless;
}
.borderless--cian{
    @include btn__borderless-color($color_cian);
}
.borderless--azul{
    @include btn__borderless-color($color_azul);
}
.borderless--blanco{
    @include btn__borderless-color(#ffffff);
}
.btn__borderless.displayOn{
    .seudo-text:before{
        display: inline-block;
        content: 'menos';
    }
}