        right: 0;
        top: 0;
        bottom: 0;
        background: rgba(0,0,0,.25);
        z-index: 0;
    }
}
.shadow__background--medium{
    &:before{
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: rgba(0,0,0,.40);
        z-index: 0;
    }
}
