//Nuevos estilos Arrows & Dots natSliders
@mixin root-arrows{
    width: 40px;
    height: 40px;
    top: 50%;
    display: none !important;
    @media only screen and (min-width: 992px){
        display: inline-block !important;
    }
    &:before{
        background: 0 0;
        font-size: 40px;
        padding: 0;
        color: $color_texto;
    }
}

@mixin root-dots {
    line-height: 0;
        li{
            width: 10px;
            height: 10px;
            button{
                border-radius: 100px;
                padding: 0;
                width: 100%;
                height: 100%;
                background: #B6B7B7;              
                &:before{
                    display: none;
                }
            }
            button[aria-label="1 of 1"]{
                display: none;
            }
        }
        .slick-active{
            button{
                background: $color_texto;    
            }
        }
}

//Arrows variacion color
@mixin root-arrows-cian {
    .slick-prev,
    .slick-next{
        &:before{
            color: $color_cian;
        }
    }  
}

@mixin root-arrows-azul {
    .slick-prev,
    .slick-next{
        &:before{
            color: $color_azul;
        }
    }
}

@mixin root-arrows-blanco {
    .slick-prev,
    .slick-next{
        &:before{
            color: #ffffff;
        }
    } 
}

//Dots variacion color
@mixin root-dots-gris-cian {    
    li{
        button{
            background: #B6B7B7 !important;
        }
    }
    .slick-active{
        button{
            background: $color-cian !important;
        }
    }
}
@mixin root-dots-gris-blanco {    
    li{
        button{
            background: #B6B7B7 !important;
        }
    }
    .slick-active{
        button{
            background: #ffffff !important;
        }
    }
}

@mixin root-dots-blanco-cian{    
    li{
        button{
            background: #ffffff !important;
        }
    }
    .slick-active{
        button{
            background: $color_cian !important;
        }
    }
}

.natSlick_navigation{
    .slick-next,
    .slick-prev{
        @include root-arrows;        
    }
    .slick-next{
        right: 0;
    }
    .slick-prev{
        left: 0;
    }
    .slick-dots {
        @include root-dots;
    }
}

//atributos variacones de color navigation
[arrw-cian]{
    @include root-arrows-cian;
}
[arrw-azul]{
    @include root-arrows-azul;
}
[arrw-blanco]{
    @include root-arrows-blanco;
}
[dts-gris-cian]{
    @include root-dots-gris-cian;
}
[dts-gris-blanco]{
    @include root-dots-gris-blanco;
}
[dts-blanco-cian]{
    @include root-dots-blanco-cian;
}

//Picture image nueva linea
.picture__image{
    object-fit: cover;
    object-position: center top;
    height: 100%;
    max-width: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: -1;
}

//blur effect
.item__blur{
    backdrop-filter: blur(15px); 
    background: rgba(255,255,255,.1);
}